у ленты.\nУцелели только «Бедный Пьеро» и «Вокруг кабины» (Autour dun cabine).")])]) : _vm._e(), _vm._v(" "), (_vm.$route.params.lang === 'en') ? _c('div', [_c('p', [_vm._v("The Théâtre Optique was a moving picture show presented by Charles-Émile Reynaud in 1892, based on the praxinoscope that allows to offer a show from the projection of mobile cartoons lasting between six and fifteen minutes. This is he combination of a magic lantern which projected images in the background of the scene, and another one which projected the figures painted by mirrors and lenses in plates in a band of perforated film. This is one of the precedents of the cinematographer although the images had to be drawn by hand on the film and it was the first presentation of projected moving images to an audience, predating Auguste and Louis Lumière's first public performance by three years.")]), _vm._v(" "), _vm._m(1), _vm._v(" "), _vm._m(2), _vm._v(" "), _vm._m(3), _vm._v(" "), _vm._m(4)]) : _vm._e()])])
