évin until 1900 and over 500,000 people had seen it.\nReynaud ended up ruined and forgotten. One of the reasons for his short fame was that the size of his film tapes were too big to make copies. In 1913 Reynaud throw himself to the Seine with his last copy of a Théâtre Optique, although a few days later he would be visited by a French inventor and producer Léon Gaumont, to buy the invention and donate it to the Conservatoire des Arts et Métiers.")])
